
import XButton from 'vux/src/components/x-button/index.vue'

import { getCharges, rechargePay } from '@/api/'
import { mapActions } from 'vuex'
import Charge from '@/components/Charge'
export default {
data () {
return {
bpValueIndex: -1,
userInfo: {},
exps: [],
loading: false,
loadingPrice: false
}
},
beforeRouteEnter (to, from, next) {
document.title = '我的账户'
next()
},
created () {
let historyCount = window.sessionStorage.getItem('count') * 1 || 0
if (historyCount !== 1) {
setTimeout(() => {
window.location.reload()
}, 150)
} else {
this.getData()
}
},
components: {
XButton,
Charge
},
methods: {
...mapActions('user', [
'getUserInfo'
]),
setSelect (index) {
this.bpValueIndex = index
},
getData () {
this.loadingPrice = true
getCharges().then((res) => {
this.userInfo = res.result.user
this.exps = res.result.exp
}).finally(() => {
this.$vux.loading.hide()
this.loadingPrice = false
})
},
requestPay () {
var _self = this
if (this.bpValueIndex === -1) {
this.$vux.toast.show({
text: '请选择金额支付'
})
return false
}
this.loading = true
rechargePay({eid: this.exps[this.bpValueIndex].id, money: this.exps[this.bpValueIndex].money}).then((res) => {
window.WeixinJSBridge && window.WeixinJSBridge.invoke('getBrandWCPayRequest', res.result, function (res) {
switch (res.err_msg) {
case 'get_brand_wcpay_request:cancel':

break
case 'get_brand_wcpay_request:fail':
_self.$vux.toast.show({
text: '支付失败！（' + res.err_desc + '）',
width: '10em'
})
break
case 'get_brand_wcpay_request:ok':
_self.$vux.toast.show({
text: '支付成功'
})
setTimeout(() => {
if (localStorage.getItem('payBack') === '1') {

localStorage.removeItem('payBack')
_self.getUserInfo().then((res) => {
window.history.go(-1)
})
} else {
_self.getData()
}
}, 500)
break
default:
alert(JSON.stringify(res))
break
}
})
/* this.$wechat.chooseWXPay({
...res.result,
success: () => {
this.$vux.toast.show({
text: '支付成功'
})
}
}) */
}).finally(() => {
this.loading = false
})
}
}
}



