import InlineLoading from './InlineLoading'
export default {
  props: {
    exps: {
      type: Array,
      default: () => {
        return []
      }
    },
    loading: {
      type: Boolean,
      default: false
    },
    selectItem: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      bpValueIndex: -1
    }
  },
  methods: {
    select (i) {
      if (this.selectItem) {
        this.bpValueIndex = this.bpValueIndex === i ? -1 : i
        this.$emit('onSelect', this.bpValueIndex)
      } else {
        this.$emit('onSelect', i)
      }
    }
  },
  components: {
    InlineLoading
  }
}



